import Project from '../project';
import BaseUtils from './base/_utils';

global.Utils = Object.assign({}, BaseUtils, {
    getStatusType(type) {
        switch (type) {
            case 'REFER':
                return 'Referral';
            case 'ACCEPTED':
                return 'Accepted';
            case 'REJECTED':
                return 'Rejected';
            case 'MANUAL_UPDATE':
                return 'Manual Update';
            default: return '';
        }
    },

    toTitleCase(phrase) {
        return phrase ? phrase
            .toLowerCase()
            .split(' ')
            .map(word => word.charAt(0).toUpperCase() + word.slice(1))
            .join(' ') : '';
    },

    toParam(obj) { // {min:100,max:200} -> ?min=100&max=200
        if (!obj) return '';
        const allDefined = _.omit(obj, _.filter(Object.keys(obj), key => typeof obj[key] === 'undefined' || obj[key] === null));
        return Object.keys(allDefined).map((k) => {
            let val = allDefined[k];
            if (Array.isArray('array')) {
                val = val.join(',');
            }
            return `${encodeURIComponent(k)}=${encodeURIComponent(val)}`;
        }).join('&');
    },

    fromParam(str) { // {min:100,max:200} <- ?min=100&max=200
        if (!str && !document.location.search) {
            return {};
        }
        // eslint-disable-next-line
        const urlString= (str || document.location.search).replace(/(^\?)/, '');
        return JSON.parse(`{"${urlString.replace(/&/g, '","').replace(/=/g, '":"')}"}`, (key, value) => (key === '' ? value : decodeURIComponent(value)));
    },

    applyReferredChanges(obj, changes) { // updates based on [{"core.prop.subProp":"newValue"}}] core.prop.subProp = value
        if (!obj) {
            console.log('WARNING', 'Attempted to apply changes to a null object');
            return obj;
        }
        const newObj = _.cloneDeep(obj);
        _.each(changes, (v, key) => {
            _.set(newObj, `common_data_model.${key}`, v); // core.prop.subProp = value
        });

        return newObj;
    },

    getReasonType(type) {
        if (!type) return '';
        if (Project.debug) return type;

        // todo: while api is broke
        type = type.replace(/[\[\"\]]/g, '');
        switch (type) {
            case 'INVALID_MATCH_CRITERIA':
                return 'Not enough information';
            case 'REASON_INDISTINCT_MATCH':
                return 'Multiple matches found';
            case 'CONFLICT':
                return 'Conflict of data';
            case 'MAPPING':
                return 'Unexpected value';
            case 'ETL_DEF_VALIDATION':
                return 'Data format incorrect';
            case 'CLASSIFICATION_FAILURE':
                return 'Missing data - Racing post standard not met';
            case 'DQA_DEF_VALIDATION':
                return 'Incorrect data - Racing post standard not met';
            case 'OUT_OF_ORDER':
                return 'Out of date information';
            default:
                return `Unknown error: ${type}`;
        }
    },
});

export default Utils;
